.message {
    display: flex;
    justify-content: flex-end;
    padding-right: 50px;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 20px;
    outline: black solid thin;
    font-family: 'Montserrat', serif;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    outline: blue solid thin;
}

.imageAndText img {
    height: 48px;
    outline: lawngreen solid thin;
}

.text {
    background-color: #0066cc;
    color: #FFFFFF;
    margin-right: 15px;
    position: relative;
    padding: 10px;
    border-radius: 10px 10px 0 10px;
}

.text:after {
    content: "";
    border-left: 5px solid #0066cc;
    border-right: 5px solid transparent;
    border-top: 5px solid transparent;
    border-bottom: 5px solid #0066cc;
    position: absolute;
    bottom: 0;
    right: -10px;
}
.name {
    display: flex;
    justify-content: flex-end;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
}

.messageText {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    width: 500px;
    word-break:break-all;
}

.time {
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    margin: 5px 14px;
    /*outline: red solid thin;*/
}

